import React, { useState } from "react";
import { Button } from "./ui/button";
import { closestCorners, DndContext, DragEndEvent, useDroppable } from "@dnd-kit/core";
import Columns from "./Columns";

interface DataVisualizeProps {
  file: File;
  columns: string[];
  onDelete: () => void;
}

interface ChartColumns {
  chart1: string[];
  chart2: string[];
  chart3: string[];
  chart4: string[];
}



const DataVisualize: React.FC<DataVisualizeProps> = ({ file, columns, onDelete }) => {
  // Maintain dropped columns per chart.
  const [chartColumns, setChartColumns] = useState<ChartColumns>({
    chart1: [],
    chart2: [],
    chart3: [],
    chart4: [],
  });

  // onDragEnd will be called when a draggable item (a column) is dropped.
  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    // over?.id should match one of our chart area IDs
    if (over && typeof over.id === "string" && chartColumns.hasOwnProperty(over.id)) {
      setChartColumns((prev) => {
        const newCols = { ...prev };
        // Avoid duplicates
        if (!newCols[over.id as keyof ChartColumns].includes(active.id as string)) {
          // Allow up to 4 columns per chart.
          if (newCols[over.id as keyof ChartColumns].length < 4) {
            newCols[over.id as keyof ChartColumns] = [
              ...newCols[over.id as keyof ChartColumns],
              active.id as string,
            ];
          }
        }
        return newCols;
      });
    }
  };

  return (
    <div className="flex gap-3 w-full bg-gray-800 p-2 shadow-lg h-full">
      {/* Left side: Scrollable list of draggable columns */}
      <div className="w-1/5 bg-gray-700 p-4 rounded shadow-lg overflow-y-auto">
        <h2 className="text-xl font-semibold text-white text-center">
          {file.name}
          <br />
          Columns
        </h2>
        <div className="mt-4 text-white flex flex-col items-center justify-center">
          {columns.map((element, index) => (
            // Assuming the Columns component renders a draggable item with active.id set to the element value.
            <Columns key={index} element={element} index={index} />
          ))}
        </div>
        <Button
          className="mt-4 px-6 py-2 dark:bg-gray-500 dark:hover:bg-gray-800 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-all w-full"
          onClick={onDelete}
        >
          Upload Another File
        </Button>
      </div>

      {/* Right side: Chart areas with droppable zones */}
      <DndContext collisionDetection={closestCorners} onDragEnd={handleDragEnd}>
        <div className="w-4/5 bg-gray-900 p-6 rounded shadow-lg flex items-center justify-center">
          <div className="grid grid-cols-2 gap-4 w-full h-full">
            <ChartArea id="chart1" title="Chart 1" droppedColumns={chartColumns.chart1} />
            <ChartArea id="chart2" title="Chart 2" droppedColumns={chartColumns.chart2} />
            <ChartArea id="chart3" title="Chart 3" droppedColumns={chartColumns.chart3} />
            <ChartArea id="chart4" title="Chart 4" droppedColumns={chartColumns.chart4} />
          </div>
        </div>
      </DndContext>
    </div>
  );
};

export default DataVisualize;
